}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

[class$=__description] {
  text-align: center;
  font-size: 1.8rem;
  color: #1E293B;
  margin-bottom: 5rem;
}

.auth {
  width: min(80%, 60rem);
  margin: 0 auto;
}

.devwebcamp__grid {
  width: min(95%, 120rem);
  margin: 0 auto;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .devwebcamp__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.devwebcamp__image source, .devwebcamp__image img {
  max-width: 100%;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 1rem;
}
.devwebcamp__text {
  line-height: 1.5;
}

.paquetes__grid {
  width: min(80%, 80rem);
  margin: 0 auto;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
}
@media (min-width: 768px) {
  .paquetes__grid {
    width: min(95%, 120rem);
    margin: 0 auto;
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.paquete {
  padding: 3rem;
  border-radius: 1rem;
  border: 0.1rem solid #F8FAFC;
  box-shadow: rgba(30, 41, 59, 0.5) 0px 3px 8px;
  height: min-content;
}
.paquete__nombre {
  font-weight: bold;
  font-size: 2.6rem;
  margin: 2rem;
  text-align: center;
}
.paquete:nth-child(1) {
  order: 2;
}
@media (min-width: 768px) {
  .paquete:nth-child(1) {
    order: unset;
  }
}
.paquete__nombre {
  background: linear-gradient(to right, #007df4, #1E293B);
  background-clip: text;
  color: transparent;
}
.paquete__lista {
  line-height: 2;
}
.paquete__precio {
  font-size: 5rem;
  text-align: center;
  color: #007df4;
  font-weight: bold;
  margin: 2rem 0 0 0;
}

.agenda {
  padding: 5rem 0;
  width: min(95%, 120rem);
  margin: 0 auto;
}

.eventos__heading {
  color: #007df4;
  margin: 3rem 0;
  text-align: left;
  font-size: clamp(2rem, 8vw, 4rem);
}
.eventos__fecha {
  color: #64748B;
  margin: 3rem 0 1rem 0;
}
.eventos--workshops .eventos__heading {
  color: #009591;
}

.evento__hora {
  font-weight: bold;
}
.evento__nombre {
  font-weight: bold;
  font-size: 2.5rem;
}
.evento__informacion {
  padding: 2rem 5rem;
  border-radius: 1rem;
  background-color: #007df4;
  color: #FFFFFF;
  transition: background-color 0.2s;
}
.evento__informacion:hover {
  background-color: #0063c1;
}
.evento__introduccion {
  font-size: 1.6rem;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
@media (min-width: 768px) {
  .evento__introduccion {
    -webkit-line-clamp: 2;
  }
}
.evento__autor-info {
  display: flex;
  gap: 2rem;
  justify-content: space-between;
  align-items: center;
}
.evento__imagen-autor {
  width: 5rem;
  background-color: #FFFFFF;
  border-radius: 50%;
}
.evento__autor-nombre {
  font-weight: bold;
}

.form {
  display: flex;
  flex-direction: column;
}
.form__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.form__legend {
  color: #64748B;
  font-size: 2.2rem;
}
.form__field {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.form__container-icon {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
  margin-top: 1.5rem;
}
.form__list {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}
.form__tag {
  border-radius: 0.5rem;
  color: #FFFFFF;
  font-size: 1.4rem;
  padding: 1rem;
  background-color: #007df4;
  cursor: pointer;
}
.form__image {
  width: 20rem;
}
.form__icon {
  background-color: #1E293B;
  width: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.form__icon i {
  color: #FFFFFF;
  font-size: 2rem;
}
.form__label {
  display: block;
  font-size: 1.8rem;
  color: #1E293B;
  margin-top: 1.5rem;
}
.form__input, .form__input--social {
  width: 100%;
  padding: 1rem;
  border: 1px solid #b1c7dd;
  border-radius: 0.5rem;
}
.form__input--file {
  border: none;
}
.form__input--social {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.form__radio {
  display: flex;
  gap: 2rem;
}
.form__submit {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: bold;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 0.8rem;
}
.form__submit:hover {
  background-color: #0063c1;
  cursor: pointer;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
    align-self: flex-end;
  }
}
.form__submit--register {
  width: 100%;
  margin-top: 2rem;
}
.form .action-link {
  margin: 2rem 0;
  align-self: flex-end;
}

.action-link {
  display: block;
  text-align: center;
  margin: 2rem;
  font-size: 1.8rem;
  color: #64748B;
  transition: color 0.3s;
}
.action-link span {
  color: #00498e;
}
.action-link:hover {
  color: #1E293B;
}
.action-link:hover span {
  color: #007df4;
}

.horas {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .horas {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.horas__hora {
  color: #007df4;
  border: 0.1rem solid #007df4;
  border-radius: 0.5rem;
  padding: 1rem;
  text-align: center;
  transition: color 0.2s, background-color 0.2s;
  cursor: pointer;
}
.horas__hora--selected {
  background-color: #007df4;
  color: #FFFFFF;
}
.horas__hora--disabled {
  border: 0.1rem solid #64748B;
  color: #64748B;
  cursor: not-allowed;
  opacity: 0.5;
}

.header {
  min-height: 60rem;
  background-image: url(../img/grafico.svg), url(../img/grafico.svg), linear-gradient(to right, #1a1b15, rgba(26, 27, 21, 0.5)), image-set(url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-size: 25rem, 25rem, cover, cover;
  background-repeat: no-repeat;
  background-position: top left, bottom right, center center, center center;
}
@media (min-width: 768px) {
  .header {
    background-size: 40rem, 40rem, cover, cover;
  }
}
.header__container {
  width: min(95%, 120rem);
  margin: 0 auto;
}
.header__nav {
  padding: 5rem 0;
  display: flex;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__nav {
    justify-content: flex-end;
  }
}
.header__link {
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: bold;
  transition: color 0.2s;
}
.header__link:hover {
  color: #007df4;
}
.header__content {
  padding-top: 10rem;
}
@media (min-width: 768px) {
  .header__content {
    padding-top: 5rem;
  }
}
.header__logo {
  font-size: clamp(4rem, 8vw, 8rem);
  background: linear-gradient(to right, #007df4, #00c8c2);
  background-clip: text;
  color: transparent;
  text-align: center;
}
@media (min-width: 768px) {
  .header__logo {
    text-align: left;
    display: inline-block;
  }
}
.header__text {
  font-size: 3rem;
  font-weight: bold;
  margin: 1rem 0;
  color: #FFFFFF;
  text-align: center;
  text-transform: uppercase;
}
.header__text--mode {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .header__text {
    text-align: left;
  }
}
.header__button {
  background-color: #00c8c2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: bold;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 0.8rem;
  text-transform: uppercase;
  margin: 2rem 0;
}
.header__button:hover {
  background-color: #009591;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__button {
    width: auto;
  }
}

.bar {
  background-color: #007df4;
}
.bar__content {
  width: min(95%, 120rem);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .bar__content {
    flex-direction: row;
    justify-content: space-between;
  }
}
.bar__logo {
  color: #FFFFFF;
  text-align: center;
  font-size: clamp(2rem, 8vw, 3rem);
}
@media (min-width: 768px) {
  .bar__logo {
    text-align: left;
    margin: 0;
  }
}
.bar__logo h2 {
  margin: 0;
}

.nav {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
  }
}
.nav__link {
  color: #FFFFFF;
  font-size: 2.2rem;
  padding: 2rem;
  font-weight: bold;
  text-align: center;
}
.nav__link:hover {
  background-color: #FFFFFF;
  color: #007df4;
}

.footer {
  background-color: #007df4;
  margin-top: 3rem;
  padding-top: 3rem;
  color: #FFFFFF;
}
.footer__grid {
  width: min(95%, 120rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.footer__logo {
  color: #FFFFFF;
  text-align: center;
  font-size: clamp(2rem, 8vw, 4rem);
}
@media (min-width: 768px) {
  .footer__logo {
    text-align: left;
    margin: 0;
  }
}
.footer__text {
  line-height: 2;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__text {
    text-align: start;
  }
}
.footer__copyright {
  background-color: #0063c1;
  margin: 3rem 0 0 0;
  padding: 2rem 0;
  text-align: center;
  font-size: 1.8rem;
  font-weight: bolder;
}
.footer__copyright--regular {
  font-weight: normal;
}

.menu-redes {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 4rem 0;
}
.menu-redes__link::before {
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialised;
  font-family: "Font Awesome 6 Brands";
  transition: transform 0.1s;
}
.menu-redes__link:hover::before {
  color: #00498e;
  transform: scale(1.2);
}
.menu-redes__link:nth-of-type(1)::before {
  content: "\f39e";
}
.menu-redes__link:nth-of-type(2)::before {
  content: "\f099";
}
.menu-redes__link:nth-of-type(3):before {
  content: "\f167";
}
.menu-redes__link:nth-of-type(4):before {
  content: "\f16d";
}
.menu-redes__link:nth-of-type(5)::before {
  content: "\e07b";
}
.menu-redes__ocultar {
  display: none;
}
@media (min-width: 768px) {
  .menu-redes {
    justify-content: flex-end;
    margin-right: 3rem;
  }
}

.alert {
  width: 100%;
  height: auto;
  border-radius: 0.2rem;
  margin: 3rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.1rem 2rem;
}
.alert p {
  text-align: center;
  font-weight: bold;
  line-height: 1.5;
  font-size: 1.8rem;
  margin: 1rem 0;
}
.alert.error {
  border-left: 0.5rem solid rgb(169, 0, 0);
  background-color: #ffa9a9;
}
.alert.error p {
  color: rgb(169, 0, 0);
}
.alert.exito {
  border-left: 0.5rem solid #02db02;
  background-color: #78fe78;
}
.alert.exito p {
  color: #02a802;
}

.error {
  font-size: 1.6rem;
  margin: 0;
  color: rgb(169, 0, 0);
}

.pagination {
  display: flex;
  margin: 3rem 0;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .pagination {
    justify-content: flex-end;
  }
}
.pagination__link {
  color: #007df4;
  font-size: 1.6rem;
}
.pagination__link--text {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 0.1rem solid #007df4;
  transition: color 0.2s, background-color 0.2s;
}
.pagination__link--text:hover {
  background-color: #007df4;
  color: #FFFFFF;
}
