  text-transform: uppercase;
  font-weight: bold;
  transition: color 0.2s;
  background-color: transparent;
  border: none;
}
.dashboard__submit--logout:hover {
  color: #007df4;
  cursor: pointer;
}
.dashboard__grid {
  display: flex;
  flex: 1;
  overflow: auto;
}
.dashboard__sidebar {
  background-color: #2f405d;
  width: 12rem;
}
.dashboard__menu {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
.dashboard__link {
  width: 100%;
  color: #6f8ab6;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  gap: 1rem;
  transition: background-color 0.1s, color 0.1s;
  border-radius: 1rem;
}
.dashboard__link:hover, .dashboard__link--actual {
  color: #FFFFFF;
  background-color: #40587f;
}
.dashboard__menu-texto {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: bold;
}
.dashboard__icon {
  font-size: 2rem;
}
.dashboard__contenido {
  overflow-y: auto;
  padding: 1rem 1rem 3rem 1rem;
  width: 100%;
}
@media (min-width: 768px) {
  .dashboard__contenido {
    padding: 1rem 5rem 3rem 5rem;
  }
}
.dashboard__contenedor-boton {
  display: flex;
  justify-content: flex-end;
}
.dashboard__contenedor, .dashboard__formulario {
  margin-top: 2rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
}
.dashboard__formulario {
  width: min(80%, 80rem);
  margin: 0 auto;
  margin-top: 2rem;
}
.dashboard__boton {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: bold;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 0.8rem;
  text-transform: uppercase;
  font-size: 1.4rem;
}
.dashboard__boton:hover {
  background-color: #0063c1;
  cursor: pointer;
}
@media (min-width: 768px) {
  .dashboard__boton {
    width: auto;
  }
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__thead {
  background-color: #1E293B;
  color: #FFFFFF;
}
.table__th {
  padding: 0.5rem 1rem;
  text-align: left;
}
.table__tr:nth-child(even) {
  background-color: #e6edf4;
}
.table__td, .table__td--actions {
  padding: 1.5rem 1rem;
}
.table__td--actions {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
}
.table__action {
  all: unset;
  font-size: 1.4rem;
  cursor: pointer;
}
.table__action--edit {
  color: #007df4;
}
.table__action--delete {
  color: rgb(169, 0, 0);
}

.listado-ponentes__ponente {
  color: #1E293B;
  padding: 0.2rem;
  text-align: center;
}
.listado-ponentes__ponente:nth-child(odd) {
  background-color: #d4e1ed;
}
.listado-ponentes__ponente:nth-child(even) {
  background-color: #edf2f7;
}
.listado-ponentes__ponente:hover {
  cursor: pointer;
}